*{
  box-sizing:border-box
}

.App {
  margin: 20px;
  text-align: center;
}

.App-header.col{
  position:relative;
  margin: auto;
  padding: 20px;
  text-align:center;
  max-width: 80%;
  height:100px;
  background-color: rgb(252, 210, 210);
}

main{
  margin:20px;
}

#meme, #screenshot-div {
  position: relative;
  margin: 20px auto;
}

#meme, #memePicture, #screenshot, img {
  width: 300px;
  height: 300px;
}

#memeText {
  font-size: 25px;
  color:white;
  text-shadow: 0px 0px 10px black;
  position:absolute;
  top: 200px;
  font-family: 'Anton', sans-serif;
  width: 300px;
}

